<template>
    <div class="container" id="catalog">
        <div class="catalog_body">
            <div class="row justify-content-center">
                <carousel class="catalog_menu_mobile" :perPage="5">
                    <slide><a @click="chooseCat('table')">Столы</a></slide>
                    <slide><a @click="chooseCat('tabletop')">Столешницы</a></slide>
                    <slide><a @click="chooseCat('cupboard')">Шкафы</a></slide>
                    <slide><a @click="chooseCat('bed')">Кровати</a></slide>
                    <slide><a @click="chooseCat('chair')">Стулья</a></slide>
                    <slide><a @click="chooseCat('armchair')">Кресла</a></slide>
                    <slide><a @click="chooseCat('toy')">Детские игрушки</a></slide>
                </carousel>
            </div>
            <div class="row">
                <div class="col-md-3">
                    <ul class="catalog_menu">
                        <li class="catalog_menu_item first"><a @click="chooseCat('table')">Столы</a></li>
                        <li class="catalog_menu_item"><a @click="chooseCat('tabletop')">Столешницы</a></li>
                        <li class="catalog_menu_item"><a @click="chooseCat('cupboard')">Шкафы</a></li>
                        <li class="catalog_menu_item"><a @click="chooseCat('bed')">Кровати</a></li>
                        <li class="catalog_menu_item"><a @click="chooseCat('chair')">Стулья</a></li>
                        <li class="catalog_menu_item"><a @click="chooseCat('armchair')">Кресла</a></li>
                        <li class="catalog_menu_item"><a @click="chooseCat('toy')">Детские игрушки</a></li>
                    </ul>
                </div>
                <div class="col-md-6">
                    <div class="catalog_main_img" :style='{backgroundImage:`url("${currentProduct.img_url}")`}'>
                    </div>
                </div>
                <div class="col-md-2">
                    <div class="catalog_description">
                        <ul class="description">
                            <li>{{currentProduct.name}}</li>
                            <li>Артикул {{currentProduct.vendor}}</li>
                            <li>Номер {{currentProduct.number}}</li>
                            <li>Цена {{currentProduct.price}}р</li>
                        </ul>
                    </div>
                </div>
                <div class="col-md-2"></div>
            </div>
            <carousel :perPage="3">
                <slide v-for="(product,type) in products" v-if="product.type==choisen_cat">
                    <div @click="changeProduct(product)" :key="product.type">
                        <div class="preview_item_img" :style='{backgroundImage:`url("${product.img_url}")`}'>
                        </div>
                        <span class="preview_item_name">{{product.name}}</span>
                        <div class="preview_item_line"></div>
                    </div>
                </slide>
            </carousel>
        </div>
    </div>
</template>

<script>
    import db from '../config/firebaseinit'
    import { Carousel, Slide } from 'vue-carousel';

    export default{
        components:{
            name: 'Catalog',
            Carousel,
            Slide
        },

        data () {
            return{
                leftCorner: 0,
                rightCorner: 4,
                choisen_cat: 'table',
                choisen_prod: 0,
                products:[],
                currentProduct:{},
            }

        },
        methods:{
            changeProduct:function (product, $event) {
                this.currentProduct = product
            },
            chooseCat:function (i) {
                this.choisen_cat=i;
                this.leftCorner=0;
                this.rightCorner=4;
            },
        },
        created(){
            db.collection('products').get().then
            (querySnapshot => {
                querySnapshot.forEach(doc =>{
                    const data = {
                        'id': doc.id,
                        'product_id': doc.data().product_id,
                        'name' :doc.data().name,
                        'number' :doc.data().number,
                        'price' :doc.data().price,
                        'type' :doc.data().type,
                        'vendor' :doc.data().vendor,
                        'img_url' :doc.data().img_url,
                    }
                    this.products.push(data)
                })
                this.currentProduct = this.products[0];
            })
        }
    }
</script>
<style>
    .list-it{
        display: inline-block;
        margin-right: 10px;
    }
    .list-enter-active, .list-leave-active{
        transition: all 1s;
    }
    .list-enter, .list-leave-to{
        opacity: 0;
    }
    .VueCarousel-slide{
        padding: 20px;
    }
    .VueCarousel{
        padding-top: 2em;
    }
</style>