<template>
    <form @submit="sendEmail" method="post" action="/">
        <div class="row">
            <div class="col-md-2">
                <label for="name">Ваше имя: </label>
            </div>
            <div class="col-md-8">
                <input v-model="name" type="text" class="input" id="name" :placeholder="errors[0]">
            </div>
            <div class="col-md-2"></div>
        </div>
        <div class="row">
            <div class="col-md-2">
                <label for="tel">Телефон: </label>
            </div>
            <div class="col-md-8">
                <input v-model="tel" type="text" class="input" id="tel" :placeholder="errors[1]">
            </div>
            <div class="col-md-2"></div>
        </div>
        <div class="row">
            <div class="col-md-2">
                <label for="email">Email: </label>
            </div>
            <div class="col-md-8">
                <input v-model="email" type="email" class="input" id="email" :placeholder="errors[2]">
            </div>
            <div class="col-md-2"></div>
        </div>
        <div class="row">
            <div class="col-md-2">
                <label for="message">Сообщение:</label>
            </div>
            <div class="col-md-8">
                <textarea v-model="message" rows="5" type="text" class="input" id="message" placeholder=""></textarea>
            </div>
            <div class="col-md-2"></div>
        </div>
        <div class="row">
            <div class="col-md-9"></div>
            <div class="col-md-3">
                <button @click="checkForm" type="submit" class="button">отправить &rsaquo;</button>
            </div>
        </div>
    </form>
</template>

<script>
    export default{
        components:{
            name: 'ContactForm'
        },
        data() {
            return{
                errors: [],
                name: null,
                tel: null,
                email: null,
                message: null
            }
        },
        methods: {
            checkForm: function (e) {
                this.errors = [];

                if (!this.name) {
                    this.errors[0]='Требуется указать имя.';

                }
                if (!this.tel) {
                    this.tel = '';
                    this.errors[1]='Требуется указать телефон.';

                }else if (!this.validTel(this.tel)) {
                    this.tel = '';
                    this.errors[1]='Укажите корректный номер телефона';
                }

                if (!this.email) {
                    this.email = '';
                    this.errors[2]='Требуется указать email.';
                }else if (!this.validEmail(this.email)) {
                    this.email = '';
                    this.errors[2]='Укажите корректный адрес электронной почты.';
                }

                if (this.errors.length === 0){
                    return true;
                }

                e.preventDefault();
            },

            validEmail: function (email) {
                var re = /^(([^<>()\[\]\\.,;:\s@"]+(\.[^<>()\[\]\\.,;:\s@"]+)*)|(".+"))@((\[[0-9]{1,3}\.[0-9]{1,3}\.[0-9]{1,3}\.[0-9]{1,3}\])|(([a-zA-Z\-0-9]+\.)+[a-zA-Z]{2,}))$/;
                return re.test(email);
            },
            validTel: function (tel) {
                var re = /^((8|\+7)[\- ]?)?(\(?\d{3}\)?[\- ]?)?[\d\- ]{7,10}$/;
                return re.test(tel);
            },
            sendEmail: function (event) {
                console.log('pop');
                axios.post('/send.php', {
                    user_name: this.name,
                    user_email: this.email,
                    user_tel: this.tel,
                    text_comment: this.message
                })
                    .then(function (response) {
                        console.log(response);
                    })
                    .catch(function (error) {
                        console.log(error);
                        console.log('error')
                    });
            }
        }
    }
</script>