<template>
    <div class="container">
        <div class="card">
            <div class="card-header">
                <h3>Добавить товар</h3>
            </div>
            <div class="card-body">
                <form v-on:submit.prevent="writeProductData">
                    <div class="form-group">
                        <label>Категория товара:</label><br>
                        <select v-model="type">
                            <option>table</option>
                            <option>tabletop</option>
                            <option>Шкафы</option>
                            <option>Кровати</option>
                            <option>Стулья</option>
                            <option>Кресла</option>
                            <option>Детские игрушки</option>
                        </select>
                    </div>
                    <div class="form-group">
                        <label>Изображение товара:</label>
                        <input type="text" class="form-control" v-model="img_url"/>
                    </div>
                    <div class="form-group">
                        <label>Наименование товара:</label>
                        <input type="text" class="form-control" v-model="name"/>
                    </div>
                    <div class="form-group">
                        <label>Артикул товара</label>
                        <input type="text" class="form-control" v-model="vendor"/>
                    </div>
                    <div class="form-group">
                        <label>Номер товара</label>
                        <input type="text" class="form-control" v-model="number"/>
                    </div>
                    <div class="form-group">
                        <label>Цена товара</label>
                        <input type="text" class="form-control" v-model="price"/>
                    </div>
                    <div class="form-group">
                        <input type="submit" class="btn btn-primary" value="Добавить"/>
                    </div>
                </form>
            </div>
        </div>
    </div>
</template>

<script>
    import db from '../config/firebaseinit';

    export default{
        components:{
            name: 'AddPost',
        },

        data () {
            return {
                product_id: null,
                name: null,
                number: null,
                price: null,
                type: null,
                vendor: null,
                img_url: null
            }
        },
        methods: {
            writeProductData(){
                db.collection('products').add({
                    product_id: this.product_id,
                    name: this.name,
                    number: this.number,
                    price: this.price,
                    type: this.type,
                    vendor: this.vendor,
                    img_url: this.img_url
                })
                .then(docRef => {
                    this.$router.push('/')
                })
                .catch(error => console.log(err))
            }
        }
    }
</script>